@import url('https://fonts.googleapis.com/css2?family=Work+Sans:wght@700&display=swap');
@keyframes speen {
          0% {
          transform: rotate(0deg);
          }
          40% {
          transform: rotate(-5deg);
          }
          80% {
          transform: rotate(5deg);
          }
          100% {
          transform: rotate(0deg);
          }
}
@keyframes smoothColorWhite {
          0% {
          color: black;
          }
          100% {
          color: white;
          }

}

@keyframes translateToNew {
          0% {
          background-color: #c947fc;
          }
          25% {
          background-color: #9834bf;
          }
}
h3 {
          text-align: center;
          font-size: 1rem;
          color: #fff;
          animation-duration: 3s;
          animation-iteration-count: infinite;
          padding: 31.5px 16px;
            float: left;
}
.TopBar button {
font-family: 'Work Sans', sans-serif;
text-align: center;
          background: none;
          border: none;
          width: 100px;
          float: left;
          color: black;
          padding: 32px 16px;
}
.TopBar button.active {
cursor: pointer;
          background: none;
          border: none;
  color: white;
}
.games {
align-items: center;
          width: 100px;
          transform: translate(-55px,100%);
}
.games input {
border-radius: 20px;
          margin: 1.5px 0px;
          text-align: center;
          border-width: 0px;
}
.games button {
border-radius: 20px;
          margin: 10px 0px;
          width: 210px;
          background-color: #c947fc;
          border: none;
}
.games h4 {
width: 210px;
          text-align: center;
          color: white;
}
.join:hover {
animation-name: translateToNew;
          animation-duration: .5s;
          background-color: #9834bf;
}
.inactive:hover {
          cursor: pointer;
          background: none;
          border: none;
  animation-name: smoothColorWhite;
          animation-duration: .5s;
          color: white;
}
.TopBar {
          overflow: hidden;
          background-color: #562170;
}
.dashboard {
         margin: none;
  position: absolute;
  top: 20%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.google_login {
width: 200px;
          display: none;
          height: 30px;
          border-radius: 15px;
          background-color: #fff;
          border-color: #878787;
          border-width: 2px;
          font-family: 'Work Sans', sans-serif;
          text-align: center;
}
.gLogin {
width: 15px;
          display: none;
          height: 15px;
}
body {
background-color: #8132a8;
          font-family: 'Work Sans', sans-serif;
}